export default "<template>\r\n    <div>\r\n        <raw-loader :mytxt=\"require('!raw-loader!./BTableSimple.vue').default\"></raw-loader>\r\n        <h5>This is BTable Simple</h5>\r\n        <div>{{ sel }}</div>\r\n        <button @click=\"OnAdd\">Add</button>\r\n        <form @submit.stop.prevent=\"onSave\">\r\n            <b-table ref=\"mytable\" small :items=\"items\" :fields=\"fields\">\r\n                <template v-slot:cell()=\"{ value, item, field: { key } }\">\r\n\r\n                    <template v-if=\"sel[pri] != item[pri]\">\r\n                        <template v-if=\"item[pri] != 0\">\r\n                            <template v-if=\"key == pri\">\r\n                                {{ value }}\r\n                            </template>\r\n                            <template v-if=\"key == 'Ten'\">\r\n                                {{ value }}\r\n                            </template>\r\n                        </template>\r\n                    </template>\r\n                    <template v-if=\"(sel[pri] == item[pri] || item[pri] == 0)\">\r\n                        <template v-if=\"key == pri\">\r\n                            <span>{{ value }}</span>\r\n                        </template>\r\n                        <template v-if=\"key == 'Ten'\">\r\n                            <b-form-input autocomplete=\"off\" v-model=\"item[key]\" :name=\"key\" v-validate=\"'required'\"\r\n                                placeholder=\"\" :class=\"{ 'ko-hop-le': errors.has(key) }\">\r\n                            </b-form-input>\r\n                        </template>\r\n                    </template>\r\n\r\n\r\n                </template>\r\n                <template #cell(cmd)=\"row\">\r\n                    <template v-if=\"row.item[pri] == 0\">\r\n                        <b-button variant=\"outline\" class=\"mr-1\" size=\"sm\" @click=\"OnSave(row.item)\">\r\n                            <b-icon-hand-thumbs-up></b-icon-hand-thumbs-up>\r\n                        </b-button>\r\n                        <b-button variant=\"outline\" class=\"mr-1\" size=\"sm\" @click=\"OnCancel(row.item)\">\r\n                            <b-icon-file-x></b-icon-file-x>\r\n                        </b-button>\r\n                    </template>\r\n\r\n                    <template v-else>\r\n                        <b-button variant=\"outline\" class=\"mr-1\" v-if=\"sel[pri] != row.item[pri]\" size=\"sm\"\r\n                            @click=\"OnEdit(row.item)\">\r\n                            <b-icon-pencil-square></b-icon-pencil-square>\r\n                        </b-button>\r\n                        <b-button variant=\"outline\" class=\"mr-1\" v-if=\"sel[pri] != row.item[pri]\" size=\"sm\"\r\n                            @click=\"OnDel(row.item)\">\r\n                            <b-icon-trash></b-icon-trash>\r\n                        </b-button>\r\n                        <b-button variant=\"outline\" class=\"mr-1\" v-if=\"sel[pri] == row.item[pri]\" size=\"sm\"\r\n                            @click=\"OnSave(row.item)\">\r\n                            <b-icon-hand-thumbs-up></b-icon-hand-thumbs-up>\r\n                        </b-button>\r\n                        <b-button variant=\"outline\" class=\"mr-1\" v-if=\"sel[pri] == row.item[pri]\" size=\"sm\"\r\n                            @click=\"OnCancel(row.item)\">\r\n                            <b-icon-file-x></b-icon-file-x>\r\n                        </b-button>\r\n                    </template>\r\n                </template>\r\n            </b-table>\r\n        </form>\r\n    </div>\r\n</template>\r\n<script>\r\n\r\nimport VeeValidate from \"vee-validate\";\r\nimport Vue from \"vue\";\r\nVue.use(VeeValidate, {\r\n    classes: true,\r\n    fieldsBagName: 'veeFields'\r\n});\r\nimport vSelect from \"vue-select\";\r\nVue.component(\"v-select\", vSelect);\r\n\r\n\r\nexport default {\r\n    props: [\"ds1\"],\r\n    components: {\r\n    },\r\n    mounted() {\r\n    },\r\n    data() {\r\n        return {\r\n            pri: \"Id\",\r\n            sel: {},\r\n            items: [\r\n                { Id: 1, Ten: \"thuc 101\" },\r\n                { Id: 2, Ten: \"thuc 102\" },\r\n                { Id: 3, Ten: \"thuc 103\" },\r\n                { Id: 4, Ten: \"thuc 104\" },\r\n            ],\r\n            fields: [\r\n                { key: 'Id', sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: \"8%\" } },\r\n                //{ key: 'ClaimType', sortable: true, thClass: 'text-left', tdClass: 'align-middle', thStyle: { width: \"80%\" } },\r\n                { key: 'Ten', sortable: true, thClass: 'text-left', tdClass: 'align-middle', thStyle: { width: \"60%\" } },\r\n                { key: 'Cmd', sortable: true, thClass: 'text-center', tdClass: 'align-middle text-center', thStyle: { width: \"12%\" } },\r\n            ]\r\n        }\r\n    },\r\n    mounted(){\r\n    },\r\n    methods: {\r\n        OnAdd() {\r\n            let r = {\r\n                Id: 0,\r\n                Ten: 'Thức',\r\n            }\r\n            this.items.unshift(r);\r\n            this.$refs.mytable.refresh();\r\n        },\r\n        doCancel(item) {\r\n            for (let key in item) {\r\n                if (item[key] != this.sel[key]) {\r\n                    item[key] = this.sel[key]\r\n                }\r\n            }\r\n        },\r\n        OnCancel(item) {\r\n            if (item[this.pri] == 0) {\r\n                let index = this.items.findIndex(x => x[this.pri] == item[this.pri]);\r\n                this.items.splice(index, 1);\r\n                this.adding = false;\r\n                this.$refs.mytable.refresh();\r\n            } else {\r\n                this.doCancel(item);\r\n                this.sel = {};\r\n            }\r\n        },\r\n        OnEdit(item) {\r\n            this.sel = JSON.parse(JSON.stringify(item));\r\n            //alert(JSON.stringify(item));\r\n        },\r\n        OnDel(item) {\r\n            alert(JSON.stringify(item));\r\n        },\r\n        OnSave(item) {\r\n            this.$validator.validateAll().then((result) => {\r\n                if (!result) {\r\n                    this.toast(\"Không hợp lệ\");\r\n                    return;\r\n                }\r\n                let r = JSON.parse(JSON.stringify(item));\r\n                if (r[this.pri] == 0) {\r\n                    alert(`Add r ${JSON.stringify(r)}`);\r\n                    this.$refs.mytable.refresh();\r\n                } else {\r\n                    alert(`Update r ${JSON.stringify(r)}`);\r\n                    this.sel = {};\r\n                }\r\n            });\r\n        },\r\n        toast(msg, variant = null) {\r\n            this.$bvToast.toast(msg, {\r\n                title: `Information`,\r\n                variant: variant,\r\n                solid: true,\r\n            });\r\n        },\r\n    }\r\n}\r\n</script>\r\n  ";